.login-form {
    width: 100%;
    max-width: 20px;
    padding: 15px;
    margin: auto;
    height: 100%;
}

.inner {
    width: 450px;
    margin: auto;
    background: #252525;
    box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
    padding: 40px 55px 45px 55px;
    border-radius: 15px;
    transition: all .3s;
  }

  .outer .form-control:focus {
    border-color: #167bff;
    box-shadow: none;
  }
